.footer-container {
  height: 370px;
  width: 100vw;
  background: #626B7F;
  margin-top: 50px; }
  @media (min-width: 768px) {
    .footer-container {
      height: 200px;
      display: flex;
      flex-direction: row;
      justify-content: space-around; } }
  .footer-container .footer__list {
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 0px;
    padding-top: 50px; }
    @media (min-width: 768px) {
      .footer-container .footer__list {
        padding-top: 25px; } }
    .footer-container .footer__list .footer__list-item {
      display: flex;
      justify-content: center;
      flex-direction: column;
      list-style: none;
      margin-bottom: 20px;
      list-style: none; }
      @media (min-width: 768px) {
        .footer-container .footer__list .footer__list-item {
          margin-right: 30px; } }
      .footer-container .footer__list .footer__list-item a {
        color: white;
        text-decoration: none;
        text-align: center; }
        @media (min-width: 768px) {
          .footer-container .footer__list .footer__list-item a {
            text-align: left; } }
        .footer-container .footer__list .footer__list-item a span {
          font-family: "Lato";
          font-size: 18px;
          font-weight: 400;
          color: white; }
    .footer-container .footer__list::after {
      content: "";
      width: 80vw;
      height: 2px;
      background: white;
      margin-left: 10vw; }
      @media (min-width: 768px) {
        .footer-container .footer__list::after {
          display: none; } }
  .footer-container .footer__social {
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin-top: 30px; }
    @media (min-width: 768px) {
      .footer-container .footer__social {
        margin-top: 0px; } }
    .footer-container .footer__social a {
      text-align: center; }
      .footer-container .footer__social a span {
        font-family: "Lato";
        font-size: 18px;
        font-weight: 400;
        color: white; }
    .footer-container .footer__social .footer__social--list {
      margin-left: 50vw;
      width: 30vw;
      transform: translateX(-50%);
      display: flex;
      justify-content: space-between;
      flex-direction: row;
      margin-top: 30px; }
      @media (min-width: 768px) {
        .footer-container .footer__social .footer__social--list {
          width: 10vw;
          margin-left: 50%; } }
      .footer-container .footer__social .footer__social--list > div {
        display: flex;
        justify-content: space-between;
        flex-direction: row; }
