body {
  margin: 0; }

.apropos-container {
  margin-top: calc(60px + 30px);
  overflow-x: hidden; }
  @media (min-width: 768px) {
    .apropos-container {
      padding-left: 15%;
      padding-right: 15%; } }
  @media (min-width: 1200px) {
    .apropos-container {
      padding-left: 25%;
      padding-right: 25%; } }
  .apropos-container .a-propos__title_h2 {
    font-family: "Lato";
    font-size: 16px;
    font-weight: 500;
    color: #9F9F9F;
    text-align: center;
    text-transform: uppercase;
    margin-left: 50%;
    transform: translateX(-50%); }
  .apropos-container .a-propos__title_h1 {
    font-family: "Lato";
    font-size: 24px;
    font-weight: 700;
    color: black;
    text-align: center;
    padding-left: 15px;
    padding-right: 15px; }
    @media (min-width: 768px) {
      .apropos-container .a-propos__title_h1 {
        font-size: 28px; } }
  .apropos-container .a-propos__text-container {
    margin-top: 30px;
    padding-left: 15px;
    padding-right: 15px; }
    .apropos-container .a-propos__text-container p {
      font-family: "Lato";
      font-size: 16px;
      font-weight: 400;
      line-height: 150%;
      color: #9F9F9F; }
      @media (min-width: 768px) {
        .apropos-container .a-propos__text-container p {
          font-size: 18px; } }
  .apropos-container .a-propos__items {
    margin-top: 30px;
    padding-left: 15px;
    padding-right: 15px; }
    .apropos-container .a-propos__items img {
      width: auto;
      height: 200px;
      margin-top: 30px;
      margin-bottom: 30px;
      margin-left: 50%;
      transform: translateX(-50%); }
    .apropos-container .a-propos__items h3 {
      font-family: "Lato";
      font-size: 24px;
      font-weight: 700;
      color: #F9572D; }
      @media (min-width: 768px) {
        .apropos-container .a-propos__items h3 {
          font-size: 28px; } }
    .apropos-container .a-propos__items p {
      font-family: "Lato";
      font-size: 16px;
      font-weight: 400;
      line-height: 150%;
      color: #9F9F9F; }
      @media (min-width: 768px) {
        .apropos-container .a-propos__items p {
          font-size: 18px; } }
